  background: #009900;\n    height: 50px;\n    float: left;\n    width: 82%;\n}/*-- navbar --*/\n.system-header .navbar-brand{\n    color: #fff;\n}\n  \n/*---------------------------------------------\n  HEADER ICONS\n      Contents: \n          *Branch Switcher\n          *Messages\n-----------------------------------------------*/\n.margin-in-full{\n    width: 65%;\n    float: left;\n    height: 50px;\n}\n.header-navbar-icons{\n    height: 50px;\n    float: left;\n    text-align: center;\n    font-size: 16px; \n    width: 5%;\n    color: #fff;\n    padding: 10px 0 15px 0;\n}\n.header-navbar-icons:hover{\n    cursor: pointer;\n    background: #006600;\n}\n\n/*---------------------------------------------\n \n\n        HEADER ACCOUNT PROFILE\n\n\n-----------------------------------------------*/\n.header-navbar-nav{\n      height: 50px;\n      float: right;\n      color: #fff;\n      width: 20%;\n}\n.header-navbar-nav label{\n    font-size: 13px;\n    font-weight: 500;\n    vertical-align: middle;\n    padding-left: 10px;\n}\n.header-navbar-nav i{\n    font-size: 24px;\n     padding: 10px 0 0 5%;\n}\n.header-navbar-nav:hover{\n    cursor: pointer;\n    background: #006600;\n}\n/*---------------------------------------------\n \n \n        HEADER TOGGLER MENU\n\n\n-----------------------------------------------*/\n.navbar-menu-toggler-md{\n    height: 50px;\n    float: left;\n    text-align: center;\n    font-size: 16px;\n    color: #fff;\n    padding: 10px 0 15px 0;\n    display: none;\n}\n.navbar-menu-toggler-md:hover{\n    cursor: pointer;\n    background: #006600;\n}\n.dropdown-menu{\n  width: 300px;\n  border-radius: 0px !important;\n}\n#account{\n  height: 250px;\n  right: 0;\n  left: auto;\n  background: #009900;\n  border: 0px;\n}\n.dropdown-item{\n  width: 100%;\n  height: 40px;\n  float: left;\n  background: #fff;\n}\n.dropdown-header{\n  padding: 5px 0 10px 0;\n  width: 100%;\n  text-align: center;\n  border-bottom: solid 1px #ccc;\n}\n.dropdown-item-profile{\n  height: 200px;\n  width: 100%;\n  float: left;\n  top: 0;\n}\n.account-picture{\n  height: 150px;\n  width: 100%;\n  float: left;\n}\n.account-info{\n  height: 50px;\n  width: 100%;\n  float: left;\n  color: #fff;\n}\n.dropdown-item-button{\n  height: 50px;\n  width: 100%;\n  border-top:solid 1px #ccc;\n  float: left;\n  background: #fff;\n  border: solid 1px #009900;\n}\n.dropdown-item-button button{\n  height: 40px;\n  border-radius: 0;\n  width: 100px;\n  margin-top: 5px;\n}\n.dropdown-item-button button:hover{\n  background: #ff0000;\n  color: #fff;\n}\n\n\n\n\n\n/*---------------------------------------------------------          \n\n                  RESPONSIVE HANDLER\n\n-----------------------------------------------------------*/\n/*-------------- Medium and Large Screens for Tablets and Desktop --------------*/\n@media screen (min-width: 992px), screen and (max-width: 1199px){\n.system-header{\n      width: 23%;\n}\n.header-navbar{\n      width: 77%;\n}\n.header-navbar-nav{\n      width: 15%;\n}\n.navbar-menu-toggler-md{\n      display: none;\n}\n}\n@media screen (min-width: 768px), screen and (max-width: 991px){\n.system-header{\n      width: 100%;\n}\n.header-navbar{\n      width: 100%;\n}\n.margin-in-full{\n      display: none;\n}\n.header-navbar-nav{\n      width: 20%;\n}\n.navbar-menu-toggler-md{\n      width: 6%;\n      text-align: center;\n      display: block;\n}\n}\n\n/*-------------- Small Screen for Mobile Phones --------------*/\n@media (max-width: 767px){\n.system-header{\n      width: 100%;\n}\n.header-navbar{\n      width: 100%;\n}\n.margin-in-full{\n      display: none;\n}\n.navbar-menu-toggler-md{\n      width: 15%\n}\n.header-navbar-nav{\n      text-align: center;\n      width: 40%;\n}\n}\n"],"sourceRoot":""}]);

